import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Dog from '../../components/dog';

const initialState = {};

@observer
class Dogs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadDogs();
    }

	loadDogs() {
		const { dogStore } = this.props.stores;
		dogStore.load();
	}

	render() {
		const { dogStore } = this.props.stores;
		const { dogs } = dogStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12'>
						<h5>Hundene - {dogs.length} stk</h5>
						<div class='row'>
							{dogs && dogs.map(dog => (
								<Dog stores={this.props.stores} dog={dog} />
							))}
						</div>

						<div class='col-12'>
							<a class='btn btn-primary mt-5 float-right'
								href='/dogs/edit/new'
							>
								<i class='fas fa-plus' /> Legg til hunder
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Dogs;
